  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: #fff;
  border-radius: 50%;
  transition: all .15s ease-in-out;
}

#slider::-webkit-slider-thumb:hover {
  background: #d4d4d4;
  transform: scale(1.2);
}

#slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: #fff;
  border: 0;
  border-radius: 50%;
  transition: background .15s ease-in-out;
}

#slider::-moz-range-thumb:hover {
  background: #d4d4d4;
